import utils from './utils'
require('../css/style.css');

const canvas = document.querySelector('canvas')
const c = canvas.getContext('2d')

canvas.width = innerWidth
canvas.height = innerHeight

const mouse = {
    x: innerWidth / 2,
    y: innerHeight / 2
}


const colors = ['#8ecae6', '#219EBC',  '#FFB703','#ff9671','#ffc75f','#f9f871','#fb8500'] //'#FFF6E5',

// Event Listeners
addEventListener('mousemove', (event) => {
    mouse.x = event.clientX
    mouse.y = event.clientY
})

addEventListener('resize', () => {
    canvas.width = innerWidth
    canvas.height = innerHeight

    init()
})


addEventListener('wheel',(event) =>{
    let delta = event.deltaY;
    //write set text(index) method todo
    if (delta>0){
        if (text_index < text.length-1) {
            text_index += 1
            set_text(text_index)
        }
    }else{
        if (text_index > 1) {
            text_index -= 1
            set_text(text_index)
        }
    }

})

// scrolling Text behavior
let text_index = 0
let text = ["placeholder","Hello World.","I'm udicr.","placeholder","placeholder","This is a small", "N Body simulation.",
    "placeholder","placeholder","I'm a mathematician", "interested in applied mathematics.","placeholder","placeholder"]
function set_text(index){
    let t0 = text[Math.max(text_index-1,0)]
    let t1 = text[text_index]
    let t2 = text[Math.min(text_index+1,text.length-1)]
    document.getElementById('text-1').innerHTML = t0
    document.getElementById('text0').innerHTML = t1
    document.getElementById('text+1').innerHTML = t2
    if (t0=="placeholder"){
        document.getElementById('text-1').style.color = '#023047'
    }else{
        document.getElementById('text-1').style.color = 'rgba(255, 255, 255, .5)'
    }
    if (t1=="placeholder"){
        document.getElementById('text0').style.color = '#023047'
    }else{
        document.getElementById('text0').style.color = 'white'
    }
    if (t2=="placeholder"){
        document.getElementById('text+1').style.color = '#023047'
    }else{
        document.getElementById('text+1').style.color = 'rgba(255, 255, 255, .5)'
    }
}

// Objects
class Body {
    constructor(id, x, y, dx, dy, mass, color) {
        this.id = id
        this.x = x
        this.y = y
        this.p = [x, y]
        this.v = [dx, dy]
        this.a = [0, 0]
        this.mass = mass
        this.radius = Math.pow(mass, 1 / 3) * 3 / (4*Math.PI) *rho
        this.color = color
    }

    draw() {
        c.beginPath()
        c.arc(this.p[0], this.p[1], this.radius, 0, Math.PI * 2, false)
        c.fillStyle = this.color
        c.fill()
        c.closePath()

    }

    update() {
        //
        if (this.id == -1){
            this.x = mouse.x
            this.y = mouse.y
            this.p = [this.x,this.y]

        }else{
            oldPoints.unshift(new Point(this.x,this.y,1,this.color))
        }

        this.a = [0, 0]
        objects.forEach(object => {
            if (object.id !== this.id) {

                let d = utils.distance(this.p[0], this.p[1], object.p[0], object.p[1])

                // Collision detection goes here
                if (d<(this.radius+object.radius)){
                    //object lands on this and gets removed from list
                    this.mass += object.mass
                    this.radius = Math.pow(this.mass, 1 / 3) * 3 / (4*Math.PI) *rho
                    this.v[0] = (this.v[0]*this.mass + object.mass*object.v[0])/(this.mass+object.mass)
                    this.v[1] = (this.v[1]*this.mass + object.mass*object.v[1])/(this.mass+object.mass)
                    if (object.mass > this.mass){
                        this.color = object.color
                    }
                    let index = objects.indexOf(object)
                    objects.splice(index,1)
                    this.a[0] = 0
                    this.a[1] = 0
                } else{
                    this.a[0] += G * object.mass * (object.p[0] - this.p[0]) / Math.pow(d, 3)
                    this.a[1] += G * object.mass * (object.p[1] - this.p[1]) / Math.pow(d, 3)
                }
                }

        })
        this.v[0] += dt * this.a[0]
        this.v[1] += dt * this.a[1]
        this.p[0] += dt * this.v[0]
        this.p[1] += dt * this.v[1]
        if (Math.abs(this.p[0])>1.25*canvas.width || Math.abs(this.p[1])> 1.25*canvas.height){
            let index = objects.indexOf(this)
            objects.splice(index,1)
        }else{
            this.x = this.p[0]
            this.y = this.p[1]
            this.draw()
        }
    }
}

//OldPoints
class Point {
    constructor(x, y, radius, color) {

        this.x = x
        this.y = y
        this.radius = radius
        this.color = color
        this.age = 0
    }

    draw() {

        c.beginPath()
        c.arc(this.x, this.y, this.radius, 0, Math.PI * 2, false)
        c.fillStyle = this.color
        c.fill()
        c.closePath()

    }

    update() {
        this.age += 1
        if (this.age>tail_length){
            oldPoints.pop()
        }

    }
}



// Implementation
let rho = 1.5
let G = 6.67 * Math.pow(10, -2)
let dt = 0.1
let objects
let oldPoints
let tail_length = 200
let N
let v0 = 1.5

function init() {
    //text
    text_index = 1
    set_text(text_index)
    //N
    N = Math.floor(canvas.width*canvas.height/18000)
    //Objects
    objects = []
    for (let i = 0; i < N; i++) {
        objects.push(
            new Body(i,
                utils.randomIntFromRange(0, canvas.width),
                utils.randomIntFromRange(0, canvas.height),
                utils.randomIntFromRange(-v0, v0),
                utils.randomIntFromRange(-v0, v0),
                utils.randomIntFromRange(500, 1000),
                utils.randomColor(colors)
            )
        )
    }
    let mouseObject;
    mouseObject = new Body(-1,mouse.x,mouse.y,0,0, 4000, 'yellow')
    objects.unshift(mouseObject)

    //old points
    oldPoints = []

}

//time-step measurement
//needed?

// Animation Loop

function animate() {
    requestAnimationFrame(animate)
    c.clearRect(0, 0, canvas.width, canvas.height)

    objects.forEach(object => {
        object.update()
    })
    oldPoints.forEach(point => {
        point.update()
    })
    oldPoints.forEach(point => {
        point.draw()
    })
    if (objects.length < 3){
        init()

    }


}

init()
animate()
